/* Structure ============================================= */
:root {   
    --text-black: #020202;
}
body {
    overflow-x: hidden;
}
.contentWrapper {
    /* width: 100%; */
    max-width: 1290px;
    margin: 0 auto;
    padding-left: 32px;
    padding-right: 32px;
}
.namingWrapper {
    background: linear-gradient(316deg, #D2F1FF 15.57%, #EAF9FF 54.49%, #FFF4DE 92.04%, #FFE0CA 114.33%);
    background-blend-mode: multiply;
    position: relative;
    z-index: 4;
}
.pageTitle {
    color: var(--text-black);
    font-family: "Noe Display";
    font-size: 100px;
    font-style: normal;
    font-weight: 500;
    line-height: 90%;
    letter-spacing: -1px;
    text-align: left;
    margin: 15vh 0 0;
    max-width: 1135px;
}

/* Hero ========================================== */
.heroNaming {
    width: 100vw;
    height: calc(100vh - 64px);
    background-size: 522px, 999px;
    background-repeat: no-repeat;
    background-position: top left, right bottom;
    padding-top: 64px;
    display: flex;
    flex-direction: column;
}
.heroNaming .contentWrapper {
    width: 100%;
}
.heroNaming .tagline {
    padding: 40px 20px;
    margin-left: auto;
}
.breadCrumbs {
    margin-top: auto;
    margin-bottom: 10px;
    text-align: left;
}
.breadCrumb {
    display: flex;
    gap: 13px;
}
.breadCrumb span {
    color: #000;
    font-family: "Noto Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: 1px;
    text-transform: uppercase;
}
.hastag {
    color: var(--text-black);
    font-family: "Noto Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

/* Top Section ========================================== */
.topSection {
    position: relative;
    padding-bottom: 646px;
}
.topSection .contentWrapper {
    display: flex;
    flex-direction: column;
    gap: 310px;
    width: inherit;
}
.topSection .textContent {
    width: 50%;
    padding-top: 100px;
    max-width: 638px;
    margin-left: 50%;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 75px;
}
.namingParagraph {
    font-family: "Noto Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.namingParagraph.capitalized::first-letter {
    color: #000;
    font-family: "Noe Display";
    font-size: 100px;
    font-style: normal;
    font-weight: 500;
    float: left;
    margin-top: 25px;
    line-height: 50%;
}
.namingTitle40px {
    font-family: "Noe Display";
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
}
.namingQuote {
    padding-left: 35px;
    position: relative;
}
.namingQuote::before {
    content: "";
    width: 7px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--text-black);
}
.namingQuote li {
    color: var(--text-black);
    font-family: "Noe Display";
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.topSection .interactionContent {
    width: 100%;
    max-width: 638px;
    margin-left: auto;
    text-align: left;
    display: flex;
    flex-direction: column;
}
.interactionContent h3 {
    color: var(--text-black);
    font-family: "Noe Display";
    font-size: 200px;
    font-style: normal;
    font-weight: 500;
    line-height: 75%;
    text-transform: uppercase;
    margin: 0;
}
.interactionContent h3:nth-of-type(2) {
    padding-left: 115px;
}
.interactionContent .gradientButton {
    width: 250px;
    margin-top: 60px;
}
.topSection .floatingImage {
    position: absolute;
    height: auto;
    visibility: visible;
    left:0px;
    transition: all 1s ease-out;
    max-width: 40%;
}
.topSection .floatingImage:nth-of-type(1) {
    width: 603px;
    top: 110px;
    left: -110px;
    z-index: 1;
}
.topSection .floatingImage:nth-of-type(2) {
    width: 560px;
    top: 530px;
    left: -177px;
    z-index: 2;
}
.topSection .floatingImage:nth-of-type(3) {
    width: 376px;
    top: 1270px;
    left: -50px;
    z-index: 3;
}
.topSection .floatingImage:nth-of-type(4) {
    width: 595px;
    top: 1517px;
    left: -160px;
    z-index: 4;
}
.topSection .floatingImage:nth-of-type(5) {
    width: 535px;
    top: 1906px;
    left: 80px;
    z-index: 5;
}
.topSection .floatingImage:nth-of-type(6) {
    width: 301px;
    top: 2016px;
    left: 10px;
    z-index: 6;
}
.topSection .floatingImage:nth-of-type(7) {
    width: 526px;
    top: 2174px;
    left: 90px;
    z-index: 7;
}
.topSection .floatingImage:nth-of-type(8) {
    width: 579px;
    top: 1952px;
    left: -52px;
    z-index: 8;
}
.topSection .floatingImage:nth-of-type(9) {
    width: 360px;
    top: 2485px;
    left: -58px;
    z-index: 9;
}
.topSection .floatingImage:nth-of-type(10) {
    width: 1103px;
    top: 2406px;
    left: 11px;
    z-index: 5;
}
.topSection .floatingImage:nth-of-type(11) {
    width: 581px;
    top: 2514px;
    left: 721px;
    z-index: 4;
}

.topSection.showDemo .floatingImage:not(.demoImage){
    left:-1000px;
    visibility: hidden;
}

.topSection.showDemo .floatingImage.demoImage{    
    left:200px;
    visibility: visible;
}

.topSection .sourcing{    
    background:#202020;
    padding:10px;
    color:white;
    text-align: center;
    font-family: B612 Mono;
    font-size: 37px;
    font-weight: 400;
    line-height: 45px;
    letter-spacing: 0em;
    z-index: 8;
    position: absolute;
    width: 100%;
    left: 0px;
    top:calc(50% - 100px);    
}

.topSection .hideDemo{
    cursor: pointer;
    position: absolute;
    bottom:100px;
    left:100px;
}

.topSection .demo-content{
    height:100vh;
    position: relative;
    z-index: 99999;
}

.topSection .demo-content h2{
    font-family: Noe Display;
    font-size: 60px;
    font-weight: 500;
    line-height: 66px;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 0px;
}

.topSection .demo-content h4{
    font-family: Noe Display;
    font-size: 40px;
    font-weight: 500;
    line-height: 44px;
    letter-spacing: 0em;
    text-align: left;
}

.topSection .demo-content p{
    font-family: Noto Sans;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
    margin-top:0px;
}

.topSection .demo-content .buttons{
    display: flex;
    align-content: center;
    justify-content: space-between;
    flex-wrap: wrap;
}


.topSection .demo-content .result-demo{
    position: relative;
    height:682px;
}

.topSection .demo-content .result-demo .img-fluid{
    position: absolute;
    width:auto;
    height: 100%;
    right:0px;
}

.topSection .demo-content .result-demo .gradientButton{    
    z-index: 8;
    position: absolute;
    right:-150px;
    bottom:30px;
}


/* Meet firmi ================================= */
.meetFirmi {
    width: 100%;
    position: relative;
    background-color: var(--Lippincott-Gray-8);
    color: white;
}
.firmiWrapper {
    display: flex;
    flex-wrap: wrap;
}
.firmiColumnOne {
    flex: 0 0 auto;
    width: 43%;
    text-align: right;
    display: flex;
    justify-content: center;
    align-items: center;
}
.firmiColumnOne > div {
    padding: 0 72px;
}
.firmiColumnOne.second {
    align-items: flex-start;
}
.firmiColumnOne img {
    max-width: 100%;
    height: auto;
    width: 482px;
}
.firmiColumnOne h2 {
    font-family: "Noe Display";
    font-size: 60px;
    font-style: normal;
    font-weight: 500;
    line-height: 110%;
    text-align: left;
}
.firmiColumnTwo {
    flex: 0 0 auto;
    width: 57%;
    background-color: #000;
    text-align: left;
}
.paddingTextOne {
    padding-top: 250px;
    padding-bottom: 100px;
}
.twoTextWrap {
    max-width: 638px;
    padding-left: 110px;
}
.twoTextWrap h2 {
    font-family: "Noe Display";
    font-size: 100px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
    margin: 0 0 100px;
}
.twoTextWrap p {
    font-family: "Noto Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0 0 75px;
}
.twoTextWrap h3 {
    font-family: "Noe Display";
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 110%;
    margin: 0 0 75px;
}
.twoTextWrap ul {
    padding: revert;
    margin-bottom: 75px;
}
.twoTextWrap ul li {
    list-style: disc;
    font-family: "Noe Display";
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}
.twoTextWrap ul li + li {
    margin-top: 32px;
}
.twoTextWrap img {
    max-width: 100%;
    height: auto;
}
.firmiDivider {
    width: 90%;
    margin: 0 auto;
    height: 1px;
    background-color: white;
    margin-bottom: 50px;
}

/* Responsive ================================= */
@media (min-width: 768px) {
    .contentWrapper {
      padding-left: 30px;
      padding-right: 30px
    }
}
  
@media (min-width: 1024px) {
    .contentWrapper {
      padding-left: 20px;
      padding-right: 20px
    }
}

@media all and (max-width: 767px){
    .contentWrapper {
      padding-left: 15px;
      padding-right: 15px;
      width:calc(100% - 30px) !important;
    }
    .topSection .floatingImage{
        z-index: 0;
    }

    .pageTitle, .interactionContent h3{
        font-size:4rem;
        margin-top:25px;
    }

    .topSection .textContent{

    }

    .topSection .floatingImage:nth-of-type(1) {        
        top: 110px;
        left: -40px;        
    }
    .topSection .floatingImage:nth-of-type(2) {        
        top: 230px;
        left: -55px;
        z-index: 0;
    }
    .topSection .floatingImage:nth-of-type(3) {        
        top: 410px;
        left: -20px;
        z-index: 3;
    }
    .topSection .floatingImage:nth-of-type(4) {        
        top: 530px;
        left: -30px;
        z-index: 4;
    }
    .topSection .floatingImage:nth-of-type(5) {
        width: 535px;
        top: 1906px;
        left: 80px;
        z-index: 5;
    }
    .topSection .floatingImage:nth-of-type(6) {
        width: 301px;
        top: 2016px;
        left: 10px;
        z-index: 6;
    }
    .topSection .floatingImage:nth-of-type(7) {
        width: 526px;
        top: 2174px;
        left: 90px;
        z-index: 7;
    }
    .topSection .floatingImage:nth-of-type(8) {
        width: 579px;
        top: 1952px;
        left: -52px;
        z-index: 8;
    }
    .topSection .floatingImage:nth-of-type(9) {
        width: 360px;
        top: 2485px;
        left: -58px;
        z-index: 9;
    }
    .topSection .floatingImage:nth-of-type(10) {
        width: 1103px;
        top: 2406px;
        left: 11px;
        z-index: 5;
    }
    .topSection .floatingImage:nth-of-type(11) {
        width: 581px;
        top: 2514px;
        left: 0px;
        z-index: 4;
    }

    .namingQuote li{
        font-size:20px;
    }

    .interactionContent{
        text-align: center !important;
    }

    .interactionContent h3{
        padding-left:0px !important;
        font-size:120px;
    }

    .interactionContent .gradientButton{
        margin:15px auto;
    }

    .topSection .demo-content .result-demo{
        height:100vw;
    }

    .topSection .demo-content .result-demo .img-fluid{
        max-width: 100%;
        height:100vw;
    }

    .topSection .demo-content .result-demo .gradientButton{
        right: auto;
    }

}